const initialState = {
    conceptClustersWithDetails: [],
    conceptClusterConceptsDiscrete: [],
    authors:{},
    concepts:{},
    perspective:{},
    keywords:{},
    conceptCluster:{},
    authorClicked: false,
    conceptClicked: true,
    loading: true,
    apiLoading: false,
    apiServerError: false,
    apiNetworkError: false,
    conceptClusterWithDetails: {},
    sortedAuthors:[],
    initial:true
}
export default (state = initialState, action = {}) => {
    switch (action.type) {
        case 'CONCEPT_CLUSTER_DETAIL':{
            let conceptCluster=action.payload.conceptCluster[Object.keys(action.payload.conceptCluster)[0]]
            console.log("what what ",action.payload.perspectives)
            return{
                ...state,
                authors:action.payload.authors,
                concepts:action.payload.concepts,
                perspective:action.payload.perspectives,
                conceptCluster:conceptCluster,
                keywords:action.payload.keywords
            }
        }
        case 'CONCEPT_CLUSTER_DETAIL_SCROLL':{
            let conceptCluster=action.payload.conceptCluster[Object.keys(action.payload.conceptCluster)[0]]
            let prev=state.perspective;
            let prevConcept=state.concepts;
            console.log("why why",action.payload.perspectives);
            Object.keys(action.payload.perspectives).map(key => {
                console.log("checking",key);
                prev[key]=action.payload.perspectives[key];
                console.log("pushing at",)
                prevConcept[Object.keys(prevConcept)[0]].Perspectives.push(key);
            })
            let prevAuthor=state.authors;
            Object.keys(action.payload.authors).map(key => {
                prevAuthor[key]=action.payload.authors[key];
            })
        
            console.log("new formed is ",prev);
            return{
                ...state,
                authors:prevAuthor,
                concepts:prevConcept,
                perspective:prev,
                conceptCluster:conceptCluster,
                keywords:action.payload.keywords
            }
        }
        case 'SORTED_AUTHOR':{
            return {
                ...state,
                sortedAuthors:action.payload,
                initial:false
            }
        }
        case 'CLICKED_STATE':{
            return{
                ...state,
                authorClicked:action.payload.authorClicked,
                conceptClicked:action.payload.conceptClicked
            }
        }
        case 'CONCEPT_CLUSTER_PERSPECTIVE_LIKE':{
            let perspectives = state.perspective;
            perspectives[action.payload.id].loves = action.payload.loves;
            perspectives[action.payload.id].isLike = action.payload.isLike;
            return{
                ...state,
                perspective: perspectives
            }
        }
        case 'SERVER_ERROR':
            return {
                apiServerError: true,
            }
        case 'NETWORK_ERROR':
            return {
                apiNetworkError: true,
            }
        case 'REQUEST_STARTED':
            return {
                apiLoading: true,
                initial:state.initial,
                sortedAuthors:state.sortedAuthors
            }
            case 'REQUEST_SUCCESSFUL':
                return {
                    ...state,
                    conceptClusterWithDetails: action.payload,
                    apiLoading: false,
                    apiServerError: false,
                    apiNetworkError: false,
                }
                case 'REQUEST_SUCCESSFUL_SCROLL':{
                    let prev=state.conceptClusterWithDetails;
                    let neww=action.payload;
                    console.log("rpev",prev);
                    neww.Concepts.map((key,index) => {
                        console.log("checking index",index)
                        console.log("previous size was",prev.Concepts[index].Perspectives);    
                        key.Perspectives.map(persp => {
                            console.log("pushing at ",index)
                            prev.Concepts[index].Perspectives.push(persp);
                        });
                        console.log("new size will be ",prev.Concepts[index].Perspectives);




                    })
                    console.log("new is ",prev);
                    return {
                        ...state,
                        conceptClusterWithDetails: prev,
                        apiLoading: false,
                        apiServerError: false,
                        apiNetworkError: false,
                    }

                }
        case "GET_CONCEPT_CLUSTER_DETAILS":
            // console.log("Dispatch Called!");
            return {
                ...state,
                conceptClustersWithDetails: [...action.payload]
            }
        case "GET_CONCEPT_CLUSTER_CONCEPTS_DISCRETE":
            return {
                ...state,
                conceptClusterConceptsDiscrete: [...action.payload],
            }
        case 'FLUSH_CONCEPT_CLUSTER':
            return {
                loading: true,
                sortedAuthors:[]
            }
        // case "GET_CONCEPT_PERSPECTIVEIDS":
        //     return {
        //         ...state,
        //         perspectiveids: [...action.payload]
        //     }
        case "LOADING_FALSE":
            return {
                ...state,
                loading: false,
            }
        default:
            return state;
    }
}